	module.exports = {"data":{"markdownRemark":{"html":"<p>The plain text football fixtures for leagues, teams, match schedules, stadiums,\nand more that you can read with the sportdb command line tool into your SQL database of choice\nnow includes the 2015/16 seasons for the <a href=\"https://github.com/openfootball/eng-england/tree/master/2015-16\">English Premier League</a>,\nthe <a href=\"https://github.com/openfootball/de-deutschland/tree/master/2015-16\">Deutsche Bundesliga</a>,\nthe <a href=\"https://github.com/openfootball/it-italy/tree/master/2015-16\">Italian Serie A</a>,\nthe <a href=\"https://github.com/openfootball/es-espana/tree/master/2015-16\">Spanish Primera División</a> and more. Any league missing? Add your league.</p>\n<p>Use <a href=\"https://github.com/openfootball/datafile\">Datafiles</a> to build youself a copy e.g.</p>\n<pre><code>$ sportdb new en2015-16     # just the 2015-16 season, please\n$ sportdb new en            # all seasons\n</code></pre>\n<p>All data, code and samples public domain. Enjoy the beautiful game. Cheers.</p>","frontmatter":{"title":"football.db - New 2015/16 Seasons - English Premier League, Bundesliga, And More","date":"25 August, 2015"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"football.db - New 2015/16 Seasons - English Premier League, Bundesliga, And More"},"fields":{"slug":"/posts/new-season/"}}},{"node":{"frontmatter":{"title":"football.db - New Build System - Welcome ./Datafile"},"fields":{"slug":"/posts/new-build-system/"}}},{"node":{"frontmatter":{"title":"beer.db - New Repo /maps - Free 'Full-Screen' Interactive Beer Maps w/ Brewery Listings"},"fields":{"slug":"/posts/new-repo-maps/"}}}]}},"pathContext":{"slug":"/posts/new-season/"}}

